<template>
  <div class="log">
    <h1>로그 테이블</h1>

    <b-table width="200" height="100"
    style="width: 100%"
    striped bordered fixed  hover
    :items="items"
    :fields="fields"
    :per-page="perPage"
    :current-page="currentPage"
    small
    ></b-table>


    <div>
      <b-pagination v-model="currentPage" :total-rows="rows"></b-pagination>
    </div>


  </div>
</template>


<script>
import item  from "@/model/test.json";

export default{
  data() {
    return {
        perPage: 15,
        rows: item.length,
        currentPage: 1,
        index:1,
        fields : [
            {key : 'LogDate', label:'날짜' ,sortable: true},
            {key : 'LogLevel', label:'레벨',sortable: true},
            {key : 'LogCode', label:'코드',sortable: true},
            {key : 'LogString', label:'비고'}
        ],
        items: item,
    }
  }
}
</script>


<style>
</style>


